.service-section {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* service container style */
.service-section .item {
  display: flex;
  align-items: center;
  width: 100%;
}

.service-section .item .txt-bx {
  text-align: center;
}

.service-section .item:nth-child(1) {
  flex-direction: column-reverse;
}

.service-section .item:nth-child(2) {
  flex-direction: column;
  margin-top: 2rem;
}

.service-section .item .img-bx {
  width: 100%;
  gap: 1rem;
  overflow: hidden;
}

.service-section .item .img-bx img {
  width: 100%;
}

/* service grid style */
.service-grid {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  margin-top: 5rem;
}

.service-grid .item {
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  overflow: hidden;
}

.service-grid .item img {
  width: 100%;
  height: 20vh; /*something wrong*/
  object-fit: cover;
}

.service-grid .item img:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.service-grid h5 {
  text-align: center;
  grid-area: span 1 / 2;
  color: blue;
  font-size: 1rem;
}

@media only screen and (min-width: 968px) {
  .service-section {
    padding: 2rem 5rem;
  }
  .service-section .item:nth-child(1),
  .service-section .item:nth-child(2) {
    flex-direction: row;
  }
  .service-section .item .img-bx {
    width: 50%;
    transition: all 250ms;
  }
  .service-section .item .img-bx:nth-child(2) {
    margin-right: 0;
  }
  .service-section .item .img-bx:hover {
    border-left: 0.8rem solid red;
    cursor: pointer;
  }
  .service-section .item .txt-bx {
    width: 50%;
  }
}
